import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/CardGame/1.png"
import image2 from "../../static/CardGame/2.png"
import image3 from "../../static/CardGame/3.png"
import image4 from "../../static/CardGame/4.png"
import image5 from "../../static/CardGame/5.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Card Game</h1>
        
        <img class="cardGame" src={image1} alt="image1"/>
        <img class="cardGame" src={image2} alt="image2"/>
        <img class="cardGame" src={image3} alt="image3"/>
        <img class="cardGame" src={image4} alt="image4"/>
        <img class="cardGame" src={image5} alt="image5"/>

        <div>
          <p>
          This project started as freelance work but at the moment the project is on stand-by. I didn't give it's name but I can show you a bit of how it looks at the moment. 
          It is a collectible card game, meant to be played online, with RPG elements and heavy emphasis on your hero card, the cards in your deck meant to support them.
          The map is randomly generated, each hex providing different benefits and affecting each class diferently.
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage